.element {
  position: relative;
  overflow: hidden;
}

.element img {
  width: 100%;
  height: max-content;
  height: 300px;
}

.stats {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  text-align: center;
  transform: translateY(100%);
  transition: transform 0.3s ease;
}

li:hover .stats {
  transform: translateY(0);
}

.stats span {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  padding: 5px;
  width: 100%;
  background: orange;
  color: white;
  border: 2px solid rgb(201, 133, 8);
}

.stats svg {
  fill: white;
  width: 32px;
  height: 32px;
}
